.wrapPrimeSection {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.wrapPrimeSection::before {
  content: "";
  display: flex;
  position: absolute;
  left: 0;
  background: url(../../../img/sectionTexture.png);
  background-color: #d6e5e5;
  opacity: 0.6;
  width: 100%;
  height: 90vh;
  z-index: -1;
}

.wrapPrimeSectionTitle {
  position: absolute;
  left: 0;
  width: 100%;
  color: white;
  background-color: #467173;
  z-index: -1;
}

.wrapPrimeSectionTitle h2 {
  margin-left: 1rem;
}

.sectionBody {
  display: flex;
}

@media (max-width: 767px) {
  .wrapPrimeSection {
    justify-content: center;
  }
}
