.getInTouchLink {
  color: #fff;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  line-height: 1.2;
  padding: 1.2rem 1.6rem 1.2rem;
  background: #312d2a;
  border-radius: 5px;
  transition: background 0.3s, color 0.3s;
}

.getInTouchLink:active {
  background: #312d2a9c;
}

.getInTouchBtn {
  outline: 0;
  border: 0;
  margin-top: 2rem;
}
