.wrapContact {
  display: flex;
  justify-content: center;
  width: 100vw;
  margin: 2rem 0;
  background-color: #f1efed;
  z-index: 1;
}

.wrapContact::before {
  position: absolute;
  content: "";
  background-image: url(../../../img/sectionTexture_1.png);
  opacity: 0.05;
  width: 100%;
  height: 48.5vh;
  z-index: 1;
}

.wrapContactTitle {
  position: absolute;
  left: 0;
  width: 100%;
  background-color: #f5f4f2;
  z-index: 10000;
}

.wrapContactTitle h2 {
  margin-left: 1rem;
}
