.wrapPortfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wrapPortfolioTitle {
  position: absolute;
  left: 0;
  width: 100%;
  background-color: #d6e5e5;
  z-index: 10000;
}

.wrapPortfolioTitle h2 {
  margin-left: 1rem;
}

.portfolioBody {
  margin: 5rem 1rem 1rem 1rem ;
  padding: 0;
  list-style: none;
}

.portfolioBodyItems {
    margin: 0.5rem 0;
}