.wrapMobileServicesInfo {
  position: relative;
  display: flex;
  flex-direction: row;
  margin-top: 6rem;
  max-width: 100vw;
  height: 100%;
  overflow-x: auto;
}

.mobileServicesInfoCard {
  height: 55vh;
  width: 100%;
  margin: 0 0.7rem;
  background-color: #f1efed;
  border-radius: 0 0 5px 5px;
}

.mobileServicesInfoHeader {
  border-radius: 5px 5px 0 0;
  background-color: #312d2a;
  color: white;
  padding: 0.8em 20px;
}

.mobileServicesInfoHeader h2 {
  margin: 0;
}

.mobileServicesInfoBody {
  width: 85vw;
  list-style: none;
  padding: 0 1rem;
  background: url(../../../../img/arrow-next-right.svg) left 99% top 50% / 20px no-repeat,
    url(../../../../img/arrow-previous-left.svg) left 1% top 50% / 20px no-repeat;
}

.mobileServicesInfoItems {
  padding: 0.1rem 0.5rem;
  text-align: center;
}

::-webkit-scrollbar {
  display: none;
}
